body {
    background-color: #ddd;
}

.dropdown-menu {
    background-color: #00722e;
    transition: all 0.3s;
}

.show {
    transition: display 3s;
}

.dropdown-item {
    /* border-radius:20%; */
    padding: 0.5rem;
    color: #ddd;
    text-align: center;
    transition: all 0.3s;

}
.dropdown-item:hover {
    background-color: #ccc;
}
nav {
    background-color: #008f39;
}

li > button {
    background-color: transparent;
    border: none;
    color: white;
}

li > button:hover {
    color: #ddd ;
}

.nav{
    padding-bottom: -10px 0;
}

.nav-item:hover{ 
    background-color: #00722e;
}

path:hover{ 
    fill: #ddd !important;
}

li {
    list-style: none;
    padding: 0.5rem 1rem !important;
    transition: all 0.2s ease-in-out;
    margin: 0 3rem;

}

a{
    color: #fff;
    text-decoration: none;
    /* padding: 3rem; */
}
a:hover {
    color: #ddd;
    transition: all 0.2s ease-in-out;
}

.container{
    justify-content: space-evenly !important;
}

.img-1 {
    margin-left: -10rem;
}

h2 {
    text-align: center;
    margin: 2rem auto !important;
}

input {
    background-color: #ddd !important;
    border:2px solid  #00722e !important;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
}

select {
    background-color: #ddd !important;
    border:2px solid  #00722e !important;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
}

select:focus {
    box-shadow: 0 0.7rem 1.5rem #00722e!important;
    transform: translateX(0.2rem) translateY(-0.1rem) !important;
    transition: all 0.2s;
}

input:focus{
    box-shadow: 0 0.7rem 1.5rem #00722e!important;
    transform: translateX(0.2rem) translateY(-0.1rem) !important;
    transition: all 0.2s;
}

.form-label {
    margin: 2rem 0 0.5rem;
}

.section_Ventas{ 
    margin: 0 auto;
}

#btnSubmit {
    margin: 0 auto !important;
}

hr.style1 {
	border-top: 4px double #8c8b8b;
	text-align: center;
}
hr.style1:after {
	content: '\002665';
	display: inline-block;
	position: relative;
	top: -15px;
	padding: 0 10px;
	background: #f0f0f0;
	color: #8c8b8b;
	font-size: 18px;
}

.point{
    cursor: pointer;
}